body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.search-form {
  text-align: center;
  margin-top: 20px;
}

.gallery {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.photo-card {
  width: 200px;
  text-align: center;
}

.stats {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

button {
  display: block;
  margin: 20px auto;
}
